.latest-news {
    &__inner {
        @include container;
        padding-top: 15px;
        padding-bottom: 15px;
    }
    
    &__header {
        @include text-form;
        text-align: center;
    }
    
    &__items-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    
    &__item {
        color: $black;
        text-decoration: none;
        text-align: center;
        margin-bottom: 30px;
        position: relative;
        
        &:hover {
            color: $yellow;
            
            &:before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 160px;
                height: 160px;
                opacity: 0.6;
                z-index: 2; 
                background-repeat: no-repeat;
                background-position: center;
                background-image: url("data:img/png;base64,");
                background-color: $yellow;
            }
            
        }
    }
    
    &__item-img {
        width: 160px;
        height: 160px;
    }
    
    &__item-text {
        display: block;
        @include text-form;
        text-transform: none;
    }
}